import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { Response } from '@angular/http';
import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ArticleLikeService {

	constructor(private AuthHttp: AuthHttp) { }

	// Get all article's likes
	getLikes(article_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id + '/up'

		return this.AuthHttp.get(url)
			.map( (res:Response) => { console.log(res) })
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Like an article
	likeArticle(article_id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id + '/up'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Unlike an article
	unlikeArticle(article_id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/articles/' + article_id + '/up'

		return this.AuthHttp.delete(url)
			.map( (res:Response) => { console.log(res) })
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



