import { Component, OnInit } from '@angular/core';

/* Services */
import { ArticlesService } from '../../../Services/Articles/articles.service';
import { ArticleLikeService } from '../../../Services/Articles/article-like.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { TrackingService } from '../../../Services/Trackings/tracking.service';
import { TokenService } from '../../../Services/Authentication/token.service';

/* Interfaces */
import { ArticleModel } from '../../../Interfaces/article-models';

@Component({
	selector: 'app-articles',
	templateUrl: './articles.component.html',
	styleUrls: ['./articles.component.scss'],
	providers: []
})
export class ArticlesComponent implements OnInit {

	articles	: ArticleModel[]
	mobile 		: boolean = false

	constructor(private ArticlesService: ArticlesService,
				private ArticleLikeService: ArticleLikeService,
				private PicturesService: PicturesService,
				private TrackingService: TrackingService,
				private TokenService: TokenService) {
	}

	ngOnInit() {
		this.initWindow();
		this.getArticles();
	}

	trackViewPage() {
		const token = this.TokenService.getToken();

		this.TrackingService.viewActivities({}, token, 8).subscribe(r => {});
	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	getArticles() {
		this.ArticlesService.getArticles().subscribe( res => {
			this.articles = this.transformTags(res.articles);
		})
	}

	likeArticle(index) {
		this.ArticleLikeService.likeArticle(this.articles[index].id, {}).subscribe( res => {
			if (!this.articles[index]['hasLiked']) {
				this.articles[index]['hasLiked'] = 1
				this.articles[index]['likes'] += 1
			} else {
				this.articles[index]['hasLiked'] = null
				this.articles[index]['likes'] -= 1
			}
		});
	}

	transformTags(articles) {
		for (let i = 0; i < articles.length; i++) {
			if (typeof articles[i]['tags'] === 'string')
				articles[i].tags = articles[i].tags.split(',');
		}
		return articles
	}

	transformTitle(title) {
		return title.replace(/ /g, '-');;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

}



