import { Component, OnInit } from '@angular/core';
import { Title } from "@angular/platform-browser";
import { Router, ActivatedRoute } from '@angular/router';

/* Services */
import { ArticlesService } from '../../../Services/Articles/articles.service';
import { ArticleLikeService } from '../../../Services/Articles/article-like.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { FacebookService, UIParams, UIResponse } from 'ngx-facebook';
import { PicturesService } from '../../../Services/Pictures/pictures.service';
import { MetaService } from '../../../Services/meta.service';

/* Interfaces */
import { ArticleModel } from '../../../Interfaces/article-models';

@Component({
	selector: 'app-article',
	templateUrl: './article.component.html',
	styleUrls: [
		'./article.component.scss',
		'../articles/articles.component.scss'
	],
	providers: [],
})
export class ArticleComponent implements OnInit {

	article 	: ArticleModel
	comments 	: Object[]
	my_id 		: number
	message 	: string
	mobile 		: boolean = false

	constructor(private ArticlesService: ArticlesService,
				private ArticleLikeService: ArticleLikeService,
				private Router: Router,
				private ActivatedRoute: ActivatedRoute,
				private TokenService: TokenService,
				private FB: FacebookService,
				private PicturesService: PicturesService,
				private Title: Title,
				private MetaService: MetaService) { }

	ngOnInit() {
		this.initWindow();
		this.initSession();
		this.initParams();
	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	/* INIT SESSION */
	initSession() {
		if (this.TokenService.getToken())
			this.my_id = this.TokenService.getToken().user.id;
	}

	/* INIT ARTICLES */
	initParams() {
		this.ActivatedRoute.params.subscribe( params => {
			if (params['id']) {
				this.getArticles(params['id']);
			} else
				this.Router.navigate(['/learn/articles']);
		});
	}

	setMetaData() {
		const url = 'https://www.wittycircle.com' + this.Router.url
		this.Title.setTitle(`${this.article['title']} | Witty`);
		this.MetaService.setMeta('description', 'Discover one great article everyday on Witty, the professional network for the entrepreneurial age.');

		this.MetaService.setMeta('og:title', `${this.article['title']}`);
		this.MetaService.setMeta('og:description', 'Discover one great article everyday on Witty, the professional network for the entrepreneurial age.');
		this.MetaService.setMeta('og:url', url);
		this.MetaService.setMeta('og:image', this.article['picture']);
	}

	getArticles(param_id) {
		this.ArticlesService.getArticles().subscribe( res => {
			this.selectArticlesBasedOnParams(param_id, res.articles);
		})
	}

	selectArticlesBasedOnParams(param_id, articles) {
		const length = articles.length

		for (let i = 0; i < length; i++) {
			if (articles[i].id == param_id) {
				this.article = articles[i];

				this.setMetaData();
				this.getArticleComments(this.article.id);
				break ;
			}
		}
	}

	getArticleComments(id) {
		this.ArticlesService.getArticleComments(id).subscribe( res => {
			this.comments = res.comments
		});
	}

	createArticleComment() {
		if (this.message) {
			this.ArticlesService.createArticleComments(this.article.id, { message: this.message }).subscribe( res => {
				if (res.success) {
					this.message = ''
					this.getArticleComments(this.article.id);
				}
			});
		}
	}

	likeArticle() {
		this.ArticleLikeService.likeArticle(this.article.id, {}).subscribe( res => {
			if (!this.article['hasLiked']) {
				this.article['hasLiked'] = 1
				this.article['likes'] += 1
			} else {
				this.article['hasLiked'] = null
				this.article['likes'] -= 1
			}
		});
	}

	shareFacebook() {
		let params: UIParams = {
			method: 'feed',
			name: this.article.title,
			link: 'https://www.wittycircle.com/learn/articles' + this.article.id + '/' + this.transformTitle(this.article.title),
			picture: this.article.picture,
			caption: 'Wittycircle.com',
			description: ''
		}

		this.FB.ui(params)
			.then((res: UIResponse) => console.log(res))
			.catch((e: any) => console.error(e));
	}

	transformTags(tags) {
		return tags.split(',');
	}

	transformTitle(title) {
		return title.replace(/ /g, '-');;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}
}



