/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { LearnComponent } from '../Components/learn/learn.component';
import { ArticleComponent } from '../Components/learn/article/article.component';
import { ArticlesComponent } from '../Components/learn/articles/articles.component';

/* Librairies */

const routes: Routes = [
	{ path: '', component: LearnComponent,
		children: [
			{ path: '', redirectTo: 'articles', pathMatch: 'full' },
			{ path: 'articles', component: ArticlesComponent },
			{ path: 'articles/:id/:title', component: ArticleComponent },
		]
	},
];

export const LEARN_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



